import Http from "Services/Http/request.interface";
const apiEndpoints = {
  produccion: "https://adpply-ws.herokuapp.com/adpply/api/v1",
  desarrollo: "http://localhost:8080/adpply/api/v1"
};

const ENV = "produccion";

const apiEndpoint = apiEndpoints[ENV];

class RegisterService {

  static register(registerForm) {
    const url = "/user/";
    return Http.post({
      url,
      options: {
        apiEndpoint,
        json: {
          user: registerForm
        }
      }
    });
  }

  static getUserFromEmail(email) {
    const query = { "profile.email": email };
    const url = `/user/?query=${JSON.stringify(query)}`;
    return Http.get({
      url,
      options: {
        apiEndpoint
      }
    })
      .then(serverResponse => {
        if (serverResponse.message === "ok") {
          return serverResponse.data.result[0];
        } else {
          return null;
        }
      })
      .catch(err => {
        console.log("Server error", err);
        return null;
      });
  }

  static getUserById(_id) {
    const query = { _id };
    const url = `/user/?query=${JSON.stringify(query)}`;
    return Http.get({
      url,
      options: {
        apiEndpoint
      }
    })
      .then(serverResponse => {
        if (serverResponse.message === "ok") {
          return serverResponse.data.result[0];
        } else {
          return null;
        }
      })
      .catch(err => {
        console.log("Server error", err);
        return null;
      });
  }

  static activeUser({ _id, email, password }) {
    const url = `/user/${_id}`;
    return Http.put({ url, options: { apiEndpoint, json: { update: { "security.password": password, active: true } } } }) 
      .then(response => {
        if(response.message === "ok" && response.data && response.data.newUser) {
          return response.data.newUser;
        } else {
          Promise.reject("Error en activación");
        }
      });
  }

  static getJwtToken({ _id, email, password }) {
    const url = `/user/${_id}/checkCredentials`;
    return Http.post({ url, options: { apiEndpoint, json: { email, password } } });
  }

  static getTotalRegisteredUsers() {
    const url = `/user/?projection=-_id active`;
    return Http.get({
      url,
      options: {
        apiEndpoint
      }
    })
      .then(serverResponse => {
        if (serverResponse.message === "ok") {
          return {totalCount: serverResponse.data.totalCount, users: serverResponse.data.result};
        } else {
          return null;
        }
      })
      .catch(err => {
        console.log("Server error", err);
        return null;
      });

  }

  static createQuestion(question) {
    const url = `/question/createMulti/`;
    return Http.post({
      url,
      options: {
        apiEndpoint,
        json: {
          questions: [question]
        }
      }
    })
      .then(serverResponse => {
        if (serverResponse.code === 200) {
          return serverResponse.data;
        } else {
          return null;
        }
      })
      .catch(err => {
        console.log("Server error", err);
        return null;
      });

  }
}

export default RegisterService;