import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import Header from "components/Header/Header.js";
import "assets/scss/material-kit-react.scss?v=1.8.0";

import ProfilePage from "views/ProfilePage/ProfilePage.js";
import { useAuth } from "Services/AuthService/authService";
import HeaderLinks from "components/Header/HeaderLinks";
import { Typography, Box } from "@material-ui/core";
import GridContainer from "components/Grid/GridContainer";
import UserRouter from "views/Users";
import QuestionsRouter from "views/Questions";

const Application = props => {
  const { isLoggedIn, setIsLoggedIn } = useAuth();
  return !isLoggedIn() ? <Redirect to="/login" /> : (
    <>
      <Header
        color="white"
        brand="Adpply"
        fixed
        rightLinks={<HeaderLinks isLoggedIn={isLoggedIn()} />}
        changeColorOnScroll={{
          height: 200,
          color: "white"
        }}
      />
      <Box pt={10} px={5}>
        <Switch>
          <Route exact path="/app/profile/" render={() => <ProfilePage />} />
          <Route path="/app/users/" render={() => <UserRouter />} />
          <Route path="/app/questions/" render={() => <QuestionsRouter />} />
        </Switch>
      </Box>
    </>
  );
};

export default Application;