import React from "react";
import RegisterService from "Services/Register/register.service";
import { useParams, useHistory } from "react-router-dom";
import { Typography, CardActions, Box, Tooltip } from "@material-ui/core";
import GridContainer from "components/Grid/GridContainer";
import Card from "components/Card/Card";
import CardHeader from "components/Card/CardHeader";
import CardBody from "components/Card/CardBody";
import Button from "components/CustomButtons/Button";
import GridItem from "components/Grid/GridItem";

const RegisteredUsers = props => {

  const history = useHistory();

  const { id } = useParams();
  const [user, setUser] = React.useState(null);
  const [totalCount, setTotalCount] = React.useState(0);
  const [activeUsers, setActiveUsers] = React.useState(0);
  const [inactiveUsers, setInactiveUsers] = React.useState(0);
  const [loading, setLoading] = React.useState(false);

  React.useState(() => {
    setLoading(true);
    RegisterService.getUserById(id)
      .then(user => {
        setUser(user);
        return RegisterService.getTotalRegisteredUsers()
      })
      .then(({ totalCount, users }) => {
        setTotalCount(totalCount);
        let active = users.filter(e => e.active).length;
        setActiveUsers(active);
        setInactiveUsers(totalCount - active);
        setLoading(false);
      })
      .catch(err => {
        setLoading(false);
        console.log("Error cargando usuario", err);
      })
  }, []);

  return loading ? <Typography> Cargando... </Typography> : (
    <Card>
      <CardHeader style={{ textAlign: "center" }} color="primary" >
        Usuarios registrados
      </CardHeader>
      <CardBody color="secondary">
        <Box display="flex" flexWrap="wrap" style={{ overflowX: "auto", overflowY: "hidden" }}>
          <Typography variant="h1" color="primary" style={{ textAlign: "center", flex: 1 }}>
            {totalCount}
          </Typography>
          <Box display="flex" flexDirection="column" width="100%" justifyContent="space-around" flex={1}>
            <Typography variant="h6" color="primary" style={{ textAlign: "center" }}>
              {activeUsers} <span style={{ fontSize: "8pt" }}>activos</span>
            </Typography>
            <Typography variant="h6" color="primary" style={{ textAlign: "center" }}>
              {inactiveUsers} <span style={{ fontSize: "8pt" }}>inactivos</span>
            </Typography>
          </Box>
        </Box>
      </CardBody>
      <CardActions style={{ justifyContent: "flex-end" }}>
        <Tooltip title="Ver usuarios registrados">
          <Button size="sm" simple color="primary" onClick={() => history.push("/app/users/list")}> Detalles </Button>
        </Tooltip>
      </CardActions>
    </Card>
  );
};

const RegisteredQuestions = props => {
  const history = useHistory();
  return (
    <Card>
      <CardHeader style={{ textAlign: "center" }} color="primary" >
        Preguntas registradas
      </CardHeader>
      <CardBody color="secondary">
        <Box display="flex" flexWrap="wrap" style={{ overflowX: "auto", overflowY: "hidden" }}>
          <Typography variant="h1" color="primary" style={{ textAlign: "center", flex: 1 }}>
            {0}
          </Typography>
        </Box>
      </CardBody>
      <CardActions style={{ justifyContent: "space-between", flexWrap: "wrap" }}>
        <Tooltip title="Ver preguntas">
          <Button size="sm" simple color="primary" onClick={() => history.push("/app/questions/list")}> Ver </Button>
        </Tooltip>
        <Tooltip title="Registrar preguntas">
          <Button size="sm" simple color="primary" onClick={() => history.push("/app/questions/create")}> Registrar </Button>
        </Tooltip >
      </CardActions>
    </Card>
  );
};

export default function ProfilePage(props) {

  return (
    <GridContainer>
      <GridItem xs={12} sm={6} md={3} lg={4}>
        <RegisteredUsers />
      </GridItem>
      <GridItem xs={12} sm={6} md={3} lg={4}>
        <RegisteredQuestions />
      </GridItem>
    </GridContainer>
  );
};


