import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import People from "@material-ui/icons/People";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Datetime from "react-datetime";
import InputLabel from '@material-ui/core/InputLabel';
import { moment } from "moment";

import styles from "assets/jss/material-kit-react/views/loginPage.js";

import image from "assets/img/bg7.jpg";

const useStyles = makeStyles(styles);

export default function LoginPage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  const [errorInputDate, setIdErrorInputDate] = React.useState(false);
  const [errorInputNumber, setIdErrorInputNumber] = React.useState(false);
  const [errorInputCi, setIdErrorInputCi] = React.useState(false);
  const [errorInputEmail, setIdErrorInputEmail] = React.useState(false);
  const [errorInputPassword, setErrorInputPassword] = React.useState(false);
  const [errorInputPasswordRepeat, setErrorInputPasswordRepeat] = React.useState(false);

  const [jsonForm, setJsonForm] = React.useState({
    name: "",
    lastName: "",
    number: "",
    payment: "DIVISAS",
    dateBorn: "",
    comunity: "CONDOMINIO",
    ci: "",
    user: "",
    ubication: "",
    password: ""
  });
  const [methoPay, setMethoPay] = React.useState("DIVISAS");
  const [comunity, setComunity] = React.useState("CONDOMINIO");
  const [password, setPassword] = React.useState("");

  setTimeout(function () {
    setCardAnimation("");
  }, 700);
  const classes = useStyles();
  const { ...rest } = props;

  const checkDateValid = (date) => {
    console.log("dateee!!!", date);
    if (typeof date === "string") {
      console.log("ERROR FECHA !!");
      setIdErrorInputDate(true);
    }
    else {
      let dateFormat = date._d.getDate().toString() + '/' + (date._d.getMonth() + 1).toString() + '/' + date._d.getFullYear().toString();

      console.log("DATW FORMAR", dateFormat);
      setJsonForm({
        ...jsonForm,
        ["dateBorn"]: dateFormat
      });
      setIdErrorInputDate(false);
    }
  }

  const inputSet = (dateInputProps, open, close) => {
    console.log("propsss", dateInputProps);
    return (
      <CustomInput
        inputProps={{ ...dateInputProps, placeholder: "Fecha de nacimiento" }}
        labelText={dateInputProps.placeholder}
        formControlProps={{
          fullWidth: true,
          error: errorInputDate
        }}
      />
    );
  }

  const validateNumber = (value) => {
    if (value.charCodeAt(value.length - 1) < 48 || value.charCodeAt(value.length - 1) > 58) {
      return false;
    }
    else {
      return true;
    }
  }

  const handleInputChange = (event) => {
    console.log("event!!", event.target);
    const value = event.target.value;
    const id = event.target.id;

    if (id === "number" || id == "ci") {
      let numberValid = validateNumber(value);
      if (numberValid) {
        setJsonForm({
          ...jsonForm,
          [id]: value
        });
        setIdErrorInputNumber(false);
        setIdErrorInputCi(false)
      }
      else {
        id == "ci" ? setIdErrorInputCi(true) : setIdErrorInputNumber(true);
      }
    }
    else if (id === "dateBorn") {
      if (errorInputNumber == false) {
        setJsonForm({
          ...jsonForm,
          [id]: value
        });
      }
      else {
        setJsonForm({
          ...jsonForm,
          [id]: ""
        });
      }
    }
    else {
      setJsonForm({
        ...jsonForm,
        [id]: value
      });
    }
    console.log("jsonForm!!", jsonForm);
  }

  const handleChange = (event) => {
    if (event.target.name == "payment")
      setMethoPay(event.target.value);
    else
      setComunity(event.target.value);

    setJsonForm({
      ...jsonForm,
      [event.target.name]: event.target.value
    });
  }

  const checkEmail = (event) => {
    if (/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]+$/.test(event.target.value)) {
      console.log("email correcto");
      setIdErrorInputEmail(false);
      setJsonForm({
        ...jsonForm,
        [event.target.id]: event.target.value
      });
    }
    else {
      console.log("email INNcorrecto");
      setIdErrorInputEmail(true);
    }
  }

  const checkValidPassword = (event) => {
    if (event.target.value.length < 8) {
      setErrorInputPassword(true);
    }
    else {
      setErrorInputPassword(false);
    }
  }

  const checkPassword = (event) => {
    console.log("password",password);
    console.log("event.target.value",event.target.value);
    if (password === event.target.value) {
      setErrorInputPasswordRepeat(false);
      setJsonForm({
        ...jsonForm,
        [event.target.id]: event.target.value
      });
    }
    else{
      setErrorInputPasswordRepeat(true);
    }
  }

  return (
    <div>
      {/*       <Header
        absolute
        color="transparent"
        brand="Material Kit React"
        rightLinks={<HeaderLinks />}
        {...rest}
      /> */}
      <div
        className={classes.pageHeader}
        style={{
          backgroundImage: "url(" + image + ")",
          backgroundSize: "cover",
          backgroundPosition: "top center"
        }}
      >
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={12} md={4}>
              <Card className={classes[cardAnimaton]}>
                <form className={classes.form} autoComplete="off">
                  <CardHeader color="primary" className={classes.cardHeader}>
                    <h4>REGISTRO</h4>
                    <div className={classes.socialLine}>
                      <Button
                        justIcon
                        href="#pablo"
                        target="_blank"
                        color="transparent"
                        onClick={e => e.preventDefault()}
                      >
                        <i className={"fab fa-twitter"} />
                      </Button>
                      <Button
                        justIcon
                        href="#pablo"
                        target="_blank"
                        color="transparent"
                        onClick={e => e.preventDefault()}
                      >
                        <i className={"fab fa-facebook"} />
                      </Button>
                      <Button
                        justIcon
                        href="#pablo"
                        target="_blank"
                        color="transparent"
                        onClick={e => e.preventDefault()}
                      >
                        <i className={"fab fa-google-plus-g"} />
                      </Button>
                    </div>
                  </CardHeader>
                  <CardBody>
                    <CustomInput
                      labelText="Nombre"
                      id="name"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        onChange: (event) => handleInputChange(event),
                        type: "text",
                        required: true,
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ) */
                      }}
                    />
                    <CustomInput
                      labelText="Apellido"
                      id="lastName"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        onChange: (event) => handleInputChange(event),
                        type: "text",
                        required: true,
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ) */
                      }}
                    />
                    <CustomInput
                      labelText={errorInputNumber ? "Error Número inválido" : "Numero de celular"}
                      id="number"
                      formControlProps={{
                        fullWidth: true,
                        error: errorInputNumber
                      }}
                      inputProps={{
                        onChange: (event) => handleInputChange(event),
                        type: "tel",
                        required: true,
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ) */
                      }}
                    />
                    <InputLabel id="demo-simple-select-label">Metodo de Pago</InputLabel>
                    <Select
                      value={methoPay}
                      name="payment"
                      className={classes.selectEmpty}
                      onChange={(event) => handleChange(event)}
                    >
                      <MenuItem id="payment" value={"BOLIVARES"}>BOLIVARES</MenuItem>
                      <MenuItem id="payment" value={"DIVISAS"}>DIVISAS</MenuItem>
                      <MenuItem id="payment" value={"CRIPTOMONEDAS"}>CRIPTOMONEDAS</MenuItem>
                    </Select>
                    <Datetime
                      closeOnSelect={true}
                      dateFormat="DD/MM/YYYY"
                      timeFormat={false}
                      inputProps={{ required: true, id: "dateBorn", placeholder: errorInputDate === false ? "Fecha de nacimiento" : "Error al ingresar fecha de nacimiento" }}
                      onChange={(value) => console.log("value_i _i", value)}
                      onBlur={(value) => checkDateValid(value)}
                      renderInput={(dateInputProps, open, close) => inputSet(dateInputProps, open, close)}
                    />
                    <InputLabel id="demo-simple-select-label">Comunidad</InputLabel>
                    <Select
                      value={comunity}
                      name="comunity"
                      className={classes.selectEmpty}
                      onChange={(event) => handleChange(event)}
                    >
                      <MenuItem id="comunity" value={"CLUB"}>CLUB</MenuItem>
                      <MenuItem id="comunity" value={"CONDOMINIO"}>CONDOMINIO</MenuItem>
                      <MenuItem id="comunity" value={"INSTITUCION"}>INSTITUCION</MenuItem>
                      <MenuItem id="comunity" value={"EQUIPO"}>EQUIPO</MenuItem>
                    </Select>
                    <CustomInput
                      labelText={errorInputCi ? "Error Número inválido" : "Numero de cédula o pasaporte"}
                      id="ci"
                      formControlProps={{
                        fullWidth: true,
                        error: errorInputCi
                      }}
                      inputProps={{
                        onChange: (event) => handleInputChange(event),
                        type: "tel",
                        required: true,
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ) */
                      }}
                    />
                    <CustomInput
                      labelText="Ubicación"
                      id="ubication"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        onChange: (event) => handleInputChange(event),
                        type: "text",
                        required: true,
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <People className={classes.inputIconsColor} />
                          </InputAdornment>
                        ) */
                      }}
                    />
                    <CustomInput
                      labelText={errorInputEmail ? "Error Correo inválido" : "Correo electrónico"}
                      id="user"
                      formControlProps={{
                        fullWidth: true,
                        error: errorInputEmail,
                      }}
                      inputProps={{
                        onBlur: (event) => checkEmail(event),
                        type: "email",
                        required: true,
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <Email className={classes.inputIconsColor} />
                          </InputAdornment>
                        ) */
                      }}
                    />
                    <CustomInput
                      labelText={errorInputPassword ? "Contraseña mayor a 8 dígitos" : "Contraseña"}
                      id="pass"
                      formControlProps={{
                        fullWidth: true,
                        error: errorInputPassword
                      }}
                      inputProps={{
                        onChange: (value) => setPassword(value.target.value),
                        onBlur: (event) => checkValidPassword(event),
                        required: true,
                        type: "password",
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <Icon className={classes.inputIconsColor}>
                              lock_outline
                            </Icon>
                          </InputAdornment>
                        ), */
                        autoComplete: "off"
                      }}
                    />
                    <CustomInput
                      labelText={errorInputPasswordRepeat ? "Las contraseñas no coinciden" : "Repetir Contraseña"}
                      id="password"
                      formControlProps={{
                        fullWidth: true,
                        error: errorInputPasswordRepeat
                      }}
                      inputProps={{
                        onBlur: (event) => checkPassword(event),
                        required: true,
                        type: "password",
/*                         endAdornment: (
                          <InputAdornment position="end">
                            <Icon className={classes.inputIconsColor}>
                              lock_outline
                            </Icon>
                          </InputAdornment>
                        ) */
                        autoComplete: "off"
                      }}
                    />
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                    <Button simple color="primary" size="lg">
                      Get started
                    </Button>
                  </CardFooter>
                </form>
              </Card>
            </GridItem>
          </GridContainer>
        </div>
{/*         <Footer whiteFont /> */}
      </div>
    </div>
  );
}
