import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";

import styles from "assets/jss/material-kit-react/views/landingPage.js";

// Sections for this page
import RegisterSection from "./Sections/RegisterSection.js";
import TeamSection from "./Sections/TeamSection.js";
import WorkSection from "./Sections/WorkSection.js";

const dashboardRoutes = ["/login-page"];

const useStyles = makeStyles(styles);

export default function LandingPage(props) {
  const classes = useStyles();
  const { ...rest } = props;
  sessionStorage.clear();
  return (
    <div>
      <Header
        color="white"
        routes={dashboardRoutes}
        brand="Adpply"
        rightLinks={<HeaderLinks isLoggedIn={false}/>}
        fixed
        changeColorOnScroll={{
          height: 400,
          color: "white"
        }} id="top"
        {...rest}
      />
      <Parallax filter image={require("assets/img/landing-background.png")}>
        <div className={classes.container}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <h1 className={classes.title}>ADPPLY.</h1>
              <h4>
                La nueva forma de ver el mercado
              </h4>
              <br />
              <Button
                color="primary"
                size="lg"
                target="_blank"
                rel="noopener noreferrer"
              >
                Participa y gana
              </Button>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div className={classes.container}>
          <RegisterSection />
          {/* <TeamSection />
          <WorkSection /> */}
        </div>
      </div>
      {/* <Footer /> */}
    </div>
  );
}
