import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import Header from "components/Header/Header.js";
import "assets/scss/material-kit-react.scss?v=1.8.0";

import ProfilePage from "views/ProfilePage/ProfilePage.js";
import { useAuth } from "Services/AuthService/authService";
import HeaderLinks from "components/Header/HeaderLinks";
import { Typography, Box } from "@material-ui/core";
import GridContainer from "components/Grid/GridContainer";
import CreateQuestions from "./Creation/CreateQuestions";

const ListQuestions = () => <Typography>List questions</Typography>;

const QuestionsRouter = props => {
  return (
    <Switch>
      <Route exact path="/app/questions/create" render={() => <CreateQuestions />} />
      <Route exact path="/app/questions/list" render={() => <ListQuestions />} />
    </Switch>
  );
};

export default QuestionsRouter;