import React from 'react';
import { Box } from '@material-ui/core';
import Button from 'components/CustomButtons/Button.js';

const ModalContent = props => {
  return (
    <Box>
      <Box className="swal-title">{props.title} </Box>
      <Box className="swal-text">{props.message} </Box>
      <Box className="swal-footer">
        <Button size="sm" color="primary" variant="text" onClick={() => props.instance.close()}> Cerrar </Button>
      </Box>
    </Box>
  )
};

export default ModalContent;