// https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API/Using_Fetch

// Creditos: https://gist.github.com/6174/6062387
const generateRandomId = () => Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);

const request = (function() {

  const fetchWrapper = method => (url, options={}) => {
    let fetchOptions = {
      method,
      mode: 'cors',
      cache: 'no-cache',
      headers: {
        'Content-Type': 'application/json',
        ...(options.headers || {})
      }
    };
    let processId;

    if (["POST", "PUT"].includes(method)) {      
      fetchOptions.body = JSON.stringify(options.json);
    }

    if (options.signal) {
      // Para anular subscripciones y evitar el error de actualizacion de estados de componentes desmontadas
      // https://medium.com/@selvaganesh93/how-to-clean-up-subscriptions-in-react-components-using-abortcontroller-72335f19b6f7
      fetchOptions.signal = options.signal;
    }

    const tryToParse = async serverResponse => {

      if(options.blockParsing) {
        return serverResponse;
      }
      
      return serverResponse.ok ? serverResponse.json() : Promise.reject(serverResponse);
    };

    // if(!options.silent) {
    //   processId = generateRandomId();
    //   SpinnerStore.dispatch({
    //     type: "ATTACH",
    //     payload: {
    //       processId
    //     }
    //   });
    // }

    return fetch(url, fetchOptions).then(tryToParse).catch(err => {
      console.log("Network connection error", err);
      return Promise.reject(err);
    })
    .finally(() => {      
      // if(!options.silent) {    
      //   SpinnerStore.dispatch({
      //     type: "DETACH",
      //     payload: {
      //       processId
      //     }
      //   });
      // }
    });
  }

  return {
    get: fetchWrapper("GET"),
    post: fetchWrapper("POST"),
    put: fetchWrapper("PUT"),
    delete: fetchWrapper("DELETE")
  };
})();

// const apiEndpoint = config.apiendpoint;

export default class Http {

  static get({url, options}) {
    let _apiEndpoint = options.apiEndpoint;
    return request.get(`${ _apiEndpoint }${ url }`, options).then(parseResponse.bind({url})).catch(handleError);
  }
  static post({url, options}) {
    let _apiEndpoint = options.apiEndpoint;
    return request.post(`${ _apiEndpoint  }${ url }`, options).then(parseResponse.bind({url})).catch(handleError);
  }
  static put({url, options}) {
    let _apiEndpoint = options.apiEndpoint;
    return request.put(`${ _apiEndpoint }${ url }`, options).then(parseResponse.bind({url})).catch(handleError);
  }
  static delete({url, options}) {
    let _apiEndpoint = options.apiEndpoint;
    return request.delete(`${ _apiEndpoint }${ url }`, options).then(parseResponse.bind({url})).catch(handleError);
  }
}

function parseResponse(response) {  
  console.log(`request-status URL(${this.url})`, response);
  return response.message === 0 ? 0 : response;
}

function handleError(err) {
  console.log("Error from server", err);
  return Promise.reject(err && err.message ? err.message : err);
}