import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import People from "@material-ui/icons/People";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Datetime from "react-datetime";
import InputLabel from '@material-ui/core/InputLabel';
import { moment } from "moment";

import styles from "assets/jss/material-kit-react/views/loginPage.js";

import image from "assets/img/bg7.jpg";
import RegisterService from "Services/Register/register.service";
import { Typography } from "@material-ui/core";
import Danger from "components/Typography/Danger";
import { useLocation, useHistory } from "react-router-dom";
import { sha256 } from "js-sha256";
import Info from "components/Typography/Info";
import AlertService from "Services/AlertService/alertService";
import { useAuth } from "Services/AuthService/authService";

const useStyles = makeStyles(styles);

const Welcome = ({ user }) => {
  return (
    <Typography>
      Bienvenido, {user.profile.name} {user.profile.lastName}
    </Typography>
  );
};

const PasswordValidator = ({ active, onPassChange }) => {

  const validatePass = (pass, check) => {
    if(active) {
      onPassChange(sha256(pass));
      return;
    }
    const isValid = pass && pass === check;
    setIsValidPass(isValid);
    if(isValid) {
      onPassChange(sha256(pass));
    } else {
      onPassChange(null);
    }
  };

  const onPasswordChange = e => {
    setPass(e.target.value);
    validatePass(e.target.value, checkPass);
  };

  const onPasswordCheckChange = e => {
    setCheckPass(e.target.value);
    validatePass(e.target.value, pass);
  };

  const [pass, setPass] = React.useState("");
  const [checkPass, setCheckPass] = React.useState("");
  const [isValidPass, setIsValidPass] = React.useState(false);

  return (
    <>
      <CustomInput
        labelText="Ingrese su contraseña"
        formControlProps={{
          fullWidth: true
        }}
        inputProps={{
          onChange: onPasswordChange,
          type: "password",
          required: true,
          error: !!checkPass && !isValidPass
        }}
      />
      {
        !active && 
        <CustomInput
          labelText="Verificación de contraseña"
          formControlProps={{
            fullWidth: true
          }}
          inputProps={{
            onChange: onPasswordCheckChange,
            type: "password",
            required: true,
            error: !!pass && !isValidPass
          }}
        />
      }
      {
        pass && checkPass && !isValidPass && 
        <Danger> Las contraseñas no coinciden </Danger>
      }
    </>
  );
}

export default function LoginPage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  const [email, setEmail] = React.useState("");
  const [loading, setLoading] = React.useState(false);
  const [user, setUser] = React.useState(null);
  const [pass, setPass] = React.useState("");
  const { setIsLoggedIn } = useAuth();

  setTimeout(function () {
    setCardAnimation("");
  }, 500);

  const classes = useStyles();
  const history = useHistory();
  const onEmailChange = e => {
    setEmail(e.target.value);
  };

  const onSubmit = async e => {
    e.preventDefault();

    if(!user) {
      onSubmitEmail();
      return;
    }

    console.log("el user", user);
    
    if(pass && !user.active) {
      const activationResp = await RegisterService.activeUser({ _id: user._id, email: user.profile.email, password: pass});
      console.log("Activation response", activationResp);
      setUser(null);
      await AlertService.success({ title: "Activación de cuenta exitosa.", message: "Por favor, inicie sesión"});      
      return;
    }

    if(pass && user.active) {
      const loginResp = await RegisterService.getJwtToken({ _id: user._id, email: user.profile.email, password: pass}).catch(err => null);
      console.log("Login response", loginResp);
      if(!loginResp) {
        return AlertService.error({title: "Error", message: "Credenciales inválidas."});
      }
      
      props.onLoginSuccess(btoa(user._id));
      history.push(`/app/profile/`);
      return;
    }
  }

  const onSubmitEmail = async e => {

    setLoading(true);
    const user = await RegisterService.getUserFromEmail(email);
    setLoading(false);
    if (!user) {
      console.log("User not found");
      AlertService.error({title: "Error", message: "El correo no se encuentra registrado"});
      return;
    }

    setUser(user);

    if (user.blocked) {
      alert("usuario bloqueado");
      return;
    }
  };

  React.useEffect(() => {
    setIsLoggedIn("");
    console.log("con efecto")
  }, []);


  return (
    <div>
      <div
        className={classes.pageHeader}
        style={{
          backgroundImage: "url(" + image + ")",
          backgroundSize: "cover",
          backgroundPosition: "top center"
        }}
      >
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem sm={12} md={6} lg={4}>
              <Card className={classes[cardAnimaton]}>
                <form className={classes.form} autoComplete="off" onSubmit={onSubmit}>
                  <CardHeader color="primary" className={classes.cardHeader} onClick={()=> history.push("/landing")}>
                    <h4>Adpply</h4>
                    {loading && <Typography> Verificando correo ... </Typography>}
                    {!loading && user && <Welcome user={user} />}
                  </CardHeader>
                  <CardBody>
                    {
                      user && !user.active && 
                      <Info>
                        Ingrese su contraseña para activar su cuenta
                      </Info>
                    }
                    {
                      !user &&
                      <CustomInput
                        labelText="Correo electronico"
                        id="name"
                        formControlProps={{
                          fullWidth: true
                        }}
                        inputProps={{
                          onChange: onEmailChange,
                          type: "email",
                          required: true,
                          disabled: loading,
                          endAdornment: (
                            <InputAdornment position="end">
                              <People className={classes.inputIconsColor} />
                            </InputAdornment>
                          )
                        }}
                      />
                    }
                    {
                      user && <PasswordValidator active={user.active} onPassChange={pass => setPass(pass)}/>
                    }
                  </CardBody>
                  <CardFooter className={classes.cardFooter}>
                    {
                      user && 
                      <Button simple variant="text" color="primary" size="sm" onClick={() => setUser(null)}>
                        Volver
                      </Button>
                    }
                    <Button color="primary" size="sm" type="submit" disabled={loading}>
                      { user ? (!user.active ? "Activar" : "Acceder") : "Siguiente" }
                    </Button>
                  </CardFooter>
                </form>
              </Card>
            </GridItem>
          </GridContainer>
        </div>
        {/*         <Footer whiteFont /> */}
      </div>
    </div>
  );
}
