import React from "react";
import swal from "@sweetalert/with-react";
import ModalContent from './modalContent.component.js';

export class AlertService {

  base({ icon, title, text }) {
    return swal({
      icon,
      content: <ModalContent title={ title } message={ text } instance={ swal }/>,
      // title, text,
      buttons: false
    });
  }

  success({ title, message: text }) {
    return this.base({
      icon: "success",
      title,
      text
    });
  }
  warning({ title, message: text }) {
    return this.base({
      icon: "warning",
      title,
      text
    });
  }
  error({ title, message: text }) {
    return this.base({
      icon: "error",
      title,
      text
    });
  }
}

let alertService = new AlertService();

export default alertService;