import React from 'react';
import { Typography, FormControlLabel, Radio, Box, LinearProgress } from '@material-ui/core';
import GridContainer from 'components/Grid/GridContainer';
import GridItem from 'components/Grid/GridItem';
import CustomInput from 'components/CustomInput/CustomInput';
import { FiberManualRecord } from '@material-ui/icons';
import Button from "components/CustomButtons/Button.js";
import RegisterService from 'Services/Register/register.service';
import AlertService from 'Services/AlertService/alertService';
import { useHistory } from 'react-router-dom';

const onlyOnce = (elem, index, self) => self.indexOf(elem) === index;
const INITIAL_STATE = {
  question: "",
  category: "",
  type: "",
  possibleAnswers: [],
  allowMultiSelection: false
};

const QuestionForm = props => {
  const [state, setState] = React.useState(INITIAL_STATE);

  const [currentAnswer, setCurrentAnswer] = React.useState("");
  const [loading, setLoading] = React.useState(false);

  const updateLabel = label => e => {
    setState({
      ...state,
      [label]: e.target.value
    });
  };

  const addPossibleAnswer = () => {
    if (!currentAnswer) return;
    setState({
      ...state,
      possibleAnswers: [
        currentAnswer,
        ...state.possibleAnswers
      ].filter(onlyOnce)
    });
    setCurrentAnswer("");
  };

  const deleteAnswer = index => {
    const { possibleAnswers } = state;

    possibleAnswers.splice(index, 1);

    setState({
      ...state,
      possibleAnswers
    });
  };

  const saveQuestion = async () => {
    console.log("el state", state);
    setLoading(true);
    const resp = await RegisterService.createQuestion(state);
    setLoading(false);
    console.log("resp del server", resp)
    if(!resp) {
      return AlertService.error({
        title: "Error al guardar el documento.",
        message: "Por favor, intente nuevamente."
      });
    }
    setState(INITIAL_STATE);
    AlertService.success({
      title: "La pregunta se ha guardado exitosamente."
    });
  };

  const history = useHistory();

  return (
    <>
      <Button simple color="primary" onClick={() => history.push("/app/profile")}>
        volver
      </Button>
      {loading && <LinearProgress />}
      <Box bgcolor="white" p={3}>
        <form>
          <GridContainer >
            <GridItem xs={12} style={{ display: "flex", justifyContent: "flex-end" }}>
              <Button color="primary" size="sm" onClick={saveQuestion} > Guardar </Button>
            </GridItem>
            <GridItem xs={12} sm={6}>
              <CustomInput
                labelText="Pregunta"
                formControlProps={{
                  fullWidth: true
                }}
                inputProps={{
                  onChange: updateLabel("question"),
                  required: true,
                  value: state.question
                }}
              />
            </GridItem>
            <GridItem xs={12} sm={6}>
              <CustomInput
                labelText="Categoria"
                formControlProps={{
                  fullWidth: true
                }}
                inputProps={{
                  onChange: updateLabel("category"),
                  required: true,
                  value: state.category
                }}
              />
            </GridItem>
            <GridItem xs={12}>
              <FormControlLabel
                control={
                  <Radio
                    checked={state.type === "free"}
                    onChange={() => updateLabel("type")({ target: { value: "free" } })}
                    value="free"
                    name="radio button enabled"
                    aria-label="free"
                    icon={
                      <FiberManualRecord
                      />
                    }
                    checkedIcon={
                      <FiberManualRecord />
                    }
                  />
                }
                label="Pregunta libre"
              />
              <FormControlLabel
                control={
                  <Radio
                    checked={state.type === "multi"}
                    onChange={() => updateLabel("type")({ target: { value: "multi" } })}
                    value="multi"
                    name="radio button enabled"
                    aria-label="free"
                    icon={
                      <FiberManualRecord
                      />
                    }
                    checkedIcon={
                      <FiberManualRecord />
                    }
                  />
                }
                label="Seleccion multiple"
              />
            </GridItem>
            {
              state.type === "multi" &&
              <GridItem xs={12} style={{ display: "flex", alignItems: "center" }}>
                <CustomInput
                  labelText="Opción"
                  formControlProps={{
                    fullWidth: true
                  }}
                  inputProps={{
                    onChange: e => setCurrentAnswer(e.target.value),
                    value: currentAnswer
                  }}
                />
                <Box>
                  <Button simple size="sm" color="primary" onClick={addPossibleAnswer}> agregar </Button>
                </Box>
              </GridItem>
            }
            <div style={{ width: "100%", maxHeight: "400px", overflowY: "auto", overFlowX: "hidden" }}>
              {
                state.type === "multi" && state.possibleAnswers.length > 0 &&
                state.possibleAnswers.map((answer, index) => (
                  <GridItem xs={12} md={6} lg={4} style={{ display: "flex", alignItems: "center" }} key={answer}>
                    <CustomInput
                      labelText="Opción"
                      formControlProps={{
                        fullWidth: true
                      }}
                      inputProps={{
                        onChange: e => setCurrentAnswer(e.target.value),
                        value: answer,
                        disabled: true
                      }}
                    />
                    <Box>
                      <Button simple size="sm" color="primary" onClick={() => deleteAnswer(index)}> eliminar </Button>
                    </Box>
                  </GridItem>
                ))
              }
            </div>
          </GridContainer>
        </form>
      </Box>
    </>
  )
}

const CreateQuestions = () => {
  return (
    <QuestionForm />
  )
};

export default CreateQuestions;