import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons
import Chat from "@material-ui/icons/Chat";
import VerifiedUser from "@material-ui/icons/VerifiedUser";
import Fingerprint from "@material-ui/icons/Fingerprint";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";

import styles from "assets/jss/material-kit-react/views/landingPageSections/productStyle.js";
import CustomInput from "components/CustomInput/CustomInput";
import Button from "components/CustomButtons/Button";

import moment from "moment";
import RegisterService from "Services/Register/register.service";
import AlertService from "Services/AlertService/alertService";

const useStyles = makeStyles(styles);

export default function ProductSection() {
  const classes = useStyles();
  const initialState = {
    name: "",
    lastName: "",
    phoneNumber: "",
    email: "",
    dateBorn: moment().format("YYY-MM-DDD")
  }
  const [userInfo, setUserInfo] = React.useState(initialState);

  const invalidString = str => {
    return !str.match(/^\w{0,15}$/);
  };
  const invalidNumber = str => {
    return !str.match(/^\d{0,15}$/);
  };
  const invalidDate = date => {
    console.log("years", date, moment().diff(moment(date), 'years'));
    return moment().diff(moment(date), 'years') < 18;
  };

  const setCustomField = (field, format="string") => e => {
    console.log("cambio", field, e.target.value);
    if(format === "string" && invalidString(e.target.value)) {
      return e.preventDefault();
    }
    if(format === "number" && invalidNumber(e.target.value)) {
      return e.preventDefault();
    }
    setUserInfo({
      ...userInfo,
      [field]: e.target.value
    })
  };

  const handleRegister = e => {
    e.preventDefault();
    if(invalidDate(userInfo.dateBorn)) {
      AlertService.error({title: "Error en el registro.", message: "Debes ser mayor de edad para registrarte."});
      return;
    }
    
    RegisterService.register(userInfo)
      .then(r => {
        console.log("response", r);
        AlertService.success({title: "Registro exitoso", message: "Inicie sesión para activar su cuenta"});
        setUserInfo(initialState);
      })
      .catch(err => {
        console.log("fallo", err);
        AlertService.error({title: "Ha ocurrido un error durante el registro", message: "Por favor, intente de nuevo."});
      });
  }

  return (
    <div className={classes.section} id="registro">
      <GridContainer justify="center">
        <GridItem xs={12} sm={12} md={8}>
          <h2 className={classes.title}>Registrate con nosotros</h2>
          <h5 className={classes.description}>
            Con solo ingresar algunos datos, quedaras participando.
          </h5>
        </GridItem>
      </GridContainer>
      <form onSubmit={ handleRegister }>
        <GridContainer>
          <GridItem sm={12} md={4}>
            <CustomInput
              labelText="Nombre"
              fullWidth={true}
              inputProps={{
                onChange: setCustomField("name"),
                required: true,
                value: userInfo.name
              }}
              formControlProps={{
                fullWidth: true
              }}
            />
          </GridItem>
          <GridItem sm={12} md={4}>
            <CustomInput
              labelText="Apellido"
              inputProps={{
                onChange: setCustomField("lastName"),
                required: true,
                value: userInfo.lastName
              }}
              formControlProps={{
                fullWidth: true
              }}
            />
          </GridItem>
          <GridItem sm={12} md={4}>
            <CustomInput
              labelText="Telefono"
              inputProps={{
                onChange: setCustomField("phoneNumber", "number"),
                required: true,
                value: userInfo.phoneNumber
              }}
              formControlProps={{
                fullWidth: true
              }}
            />
          </GridItem>
          <GridItem sm={12} md={4}>
            <CustomInput
              labelText="Correo electrónico"
              inputProps={{
                onChange: setCustomField("email", "email"),
                type: "email",
                required: true,
                value: userInfo.email
              }}
              formControlProps={{
                fullWidth: true
              }}
            />
          </GridItem>
          <GridItem sm={12} md={4}>
            <CustomInput
              labelText="Fecha de nacimiento"
              inputProps={{
                onChange: setCustomField("dateBorn", "date"),
                type: "date",
                required: true,
                value: userInfo.dateBorn
              }}
              formControlProps={{
                fullWidth: true
              }}
            />
          </GridItem>
          <GridItem sm={12} md={4}>
            <Button fullWidth simple color="primary" size="lg" type="submit">
              Registrar
            </Button>
          </GridItem>
        </GridContainer>
      </form>
    </div>
  );
}
