import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { HashRouter as Router, Route, Switch, Redirect } from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.8.0";

// pages for this product
import Components from "views/Components/Components.js";
import LandingPage from "views/LandingPage/LandingPage.js";
import ProfilePage from "views/ProfilePage/ProfilePage.js";
import LoginPage from "views/LoginPage/NewLogin.js";
import AuthService from "Services/AuthService/authService";
import { useAuth } from "Services/AuthService/authService";
import Application from "views/Main/MainRouter";

const hist = createBrowserHistory();

const Private = ({ Component, isLoggedIn }) => {
  
  return isLoggedIn() ? <Component /> : <Redirect to="/login-page" />;
};

const App = props => {
  
  const {isLoggedIn, setIsLoggedIn} = useAuth();
  
  const handleLogin = token => {
    setIsLoggedIn(token);
  };

  return (
    <Router history={hist}>
      <Switch>
        <Route path="/login" render={() => <LoginPage onLoginSuccess={ handleLogin }/>} />
        <Route path="/landing" exact component={LandingPage} />
        <Route path="/app" component={Application} />
      </Switch>
    </Router>
  );
};

export default App;
